.contactpage {
    max-height: 100vh;
    height: 90vh;
    max-width: 100vw;
    padding-top: 5rem
}

.contactpage p{
    font-size: 1rem;
    text-align: center;
}


.form-control {
    padding: 0.664rem 1rem;
    font-size: 0.95rem;
    line-height: 1.125rem;
    color: var(--tertiaryColor);
    border: 2px solid var(--darkAccent);
    font-weight: 300;
    border-radius: 0.375rem;
    transition: border-color 250ms cubic-bezier(0.27, 0.01, 0.38, 1.06), box-shadow 250ms cubic-bezier(0.27, 0.01, 0.38, 1.06);
    margin-bottom: 30px;
}

.form-control:hover {
    border-color: var(--primaryColor)
}

.form-control:focus {
    color: var(--tertiaryColor);
    border-color: var(--darkAccent);
    box-shadow: 0 0.313rem 0.719rem rgba(0, 123, 255, 0.1), 0 0.156rem 0.125rem rgba(0, 0, 0, 0.06);
}

.form-control:focus:hover {
    border-color: #007bff;
}

.form-control::-webkit-input-placeholder {
    color: var(--darkAccent);
}

.form-control:-ms-input-placeholder {
    color: var(--darkAccent);
}

.form-control::placeholder {
    color: var(--darkAccent);
}

.form-control:disabled,
.form-control[readonly] {
    background-color: black;
}

.form-control:disabled:hover,
.form-control[readonly]:hover {
    cursor: not-allowed;
}

select.form-control:hover {
    cursor: pointer;
}

form label,
form .col-form-label,
form .col-form-legend {
    font-size: 0.95rem;
}

form label:hover {
    cursor: pointer;
}

/* Error Warning */
.error-text {
    color: red !important;
}

.pass-text {
    color: green;
}



